@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap);
html, body {
  font-family: "roboto";
  margin: 0;
  padding: 0;
}

.App {
  padding: 20px;;
}

nav.navbar { 
  display: flex;
  justify-content: space-around;
  padding: 0px 20px;
  background: #666666; 
  position: fixed;
  bottom: 0;
  width: 100vw;
}

button {
  cursor: pointer;
  font-weight: bold;
}

.close-x {
  position: fixed;
  top: 15px;
  right: 15px;
  cursor: pointer;
}

.filter-tool {
    background-color : white;
    width: 100vw;
    height: 100vh;
    position: fixed;
    top: 0;
    z-index: 2;
}

.button-filter {
    width: 100vw;
    position: fixed;
    bottom: 50px;
    display: flex;
    justify-content: center;
    
}

.button-filter button {
    background-color: #c4c4c4;
    padding: 20px 40px;
    border-radius: 40px;
    border: none;
    outline: none;
    height: 60px;
}

/* ----Filter Selector overlay------ */
.selector {
    width: 100vw;
    display: flex;
    justify-content: center;
    margin: 20px;
    height: 60px;
}
.selector button {
    padding: 20px;
    border-radius: 40px;
    border: none;
    outline: none;
    width: 80vw;
    height: 60px;
}

.filterItem {
    background-color: cyan;
    text-align: center;
    padding: 15px;
}


.button-pink-fixed {
    width: 100vw;
    position: fixed;
    bottom: 50px;
    display: flex;
    justify-content: center;
    
}

.button-pink-fixed button {
    background-color: #ff0066;
    color: white;
    padding: 20px 40px;
    border-radius: 40px;
    border: none;
    outline: none;
    height: 60px;
}
/* Just like filter-tool in classes, refactor */
.comment-tool {
    background-color : white;
    width: 100vw;
    height: 100vh;
    position: fixed;
    top: 0;
    z-index: 3;
}
.text-area {
    width: 100vw;
    height: 60vh;
}
